let pagina = 1;

const cita = {
    nombre: '',
    fecha: '',
    hora: '',
    servicio: []
}

document.addEventListener('DOMContentLoaded', function(){
    iniciandoApp();
})

function iniciandoApp(){

    // Mostar servicios
    mostrarServicios();

    // Resalta el div actual segun el tab en el que se presiona
    mostarSeccion();

    // Oculta o muestra una seccion segun el tab al que se presiona 
    cambiarSeccion();

    //Paginacion Siguiente y Anterior
    paginaSiguiente();

    paginaAnterior();

    // comprueba la pagina actual actual para mostar o ocultar la paginacion (boton seiguiente o anterior) 
    botonPaginador();

    // muestra el resumen de la cita (o mensaje de error en caso de no pasar la validacion)
    mostrarResumen();

    // Almacenar el nombre de la cita en el objeto 
    nombreCita();

    //Almacena cita en el objeto
    fechaCita();

    //deshabilita dias pasados al dia de hoy
    deshabilitarFechaAnterior();

    //Almacena la hora de la cita en el objeto
    horaCita(); 

}

function mostarSeccion(){

    // Eliminar mostrar-seccion de la seccion anterior
    const seccionAnterior = document.querySelector('.mostrar-seccion');
    if(seccionAnterior){
        seccionAnterior.classList.remove('mostrar-seccion');    
    }

    const seccionActual = document.querySelector(`#paso-${pagina}`);
    seccionActual.classList.add('mostrar-seccion');

    //Eliminar la clase de actual en el tab anterior
    const tabAnterior = document.querySelector('.tabs .actual');
    if(tabAnterior){
        tabAnterior.classList.remove('actual');
    } 

    // Resalta el tab actual
    const tab = document.querySelector(`[data-paso="${pagina}"]`); // los corchetes es para seleccionar atributos de una etiqueta del html
    tab.classList.add('actual');
}

function cambiarSeccion(){
    const enlaces = document.querySelectorAll('.tabs button');

    enlaces.forEach( enlace => {
        enlace.addEventListener('click', e => {
            e.preventDefault();
            pagina = parseInt(e.target.dataset.paso);

           // llamar la funcion de mostrar seccion
           botonPaginador();
           mostarSeccion(); 
        })
    })
}

async function mostrarServicios(){
    try {

        const url = 'http://localhost:3000/servicios.php';
        
        const resultado = await fetch(url) // fetch busca el archivo y si lo encuuentra arroja un status O 
        
        const db = await resultado.json();

        //onsole.log(db);
        // const { servicios } = db;

        // Generar el HTML
        db.forEach( servicio => {
            const { id, nombre, precio } = servicio; // crea las vairables extrae el valor y se la va pasar el contenido del servicio actual

            // DOM Scripting
            // Nonmbre servicio
            const nombreServicio = document.createElement('P');
            nombreServicio.textContent = nombre;
            nombreServicio.classList.add('nombre-servicio');

            //Precio
            const precioServicio = document.createElement('P');
            precioServicio.textContent = `$ ${precio}`;
            precioServicio.classList.add('precio-servicio');

            // Generar div que contiene el nombre y precio
            const servicioDIV = document.createElement('DIV');
            servicioDIV.classList.add('servicio');
            servicioDIV.dataset.idServicio = id; // devuelve el ID del elemento HTML que se esta seleccionando

            //Selecciona un servicio para la cita 
            servicioDIV.onclick = seleccionarServicio;
            
            //Iyectar precio y nombre al DIV de servicio
            servicioDIV.appendChild(nombreServicio);
            servicioDIV.appendChild(precioServicio);

            // Inyectar al HTML todo lo anterior
            document.querySelector('#servicios').appendChild(servicioDIV);

        })

    } catch (error) {
        console.log(error);
    }
}

// Se creo esta funcion para que al dar click en cualquier parte del div lo convierta a div 
function seleccionarServicio(e){
    let elemento;

    //Forzar a que el elemento al cual demos click sea el div 
    if(e.target.tagName === 'P'){
        elemento = e.target.parentElement; // aqui le asigna el elemento padre al hijo
    }else{
        elemento = e.target;
    }

    // Seleccionar y desselecionar elemento 

    if(elemento.classList.contains('seleccionado')){
        elemento.classList.remove('seleccionado');

        const id = parseInt(elemento.dataset.idServicio);

        eleminarServicio(id);
    }else{
        elemento.classList.add('seleccionado');

        const servicioObj = {
            id: parseInt (elemento.dataset.idServicio), // pasa el ID del elemento seleccionado
            nombre: elemento.firstElementChild.textContent, // pasa el primer elemento hijo del padre
            precio: elemento.firstElementChild.nextElementSibling.textContent // pasa el segundo elemento hijo del padre
        }

        // console.log(servicioObj)

        agregarServicio(servicioObj);
    }

}

function eleminarServicio(id){
    const { servicio } = cita;
    cita.servicio = servicio.filter( servicio => servicio.id !== id);
    
    //console.log(cita);
}


function agregarServicio(servicioObj){
    const { servicio } = cita;

    cita.servicio = [...servicio, servicioObj]; // con los 3 puntos ... crea una copia del obejeto y le pasa la infomracion con otro que se rellena

    //console.log(cita);
}

function paginaSiguiente(){
    const paginaSiguiente = document.querySelector('#siguiente');
    paginaSiguiente.addEventListener('click', () =>{
        pagina ++;

        //console.log(pagina);

        botonPaginador();
    });
}

function paginaAnterior(){
    const paginaAnterior = document.querySelector('#anterior');
    paginaAnterior.addEventListener('click', () =>{
        pagina --;

       //console.log(pagina);

       botonPaginador();
    });
}

function botonPaginador(){
    const paginaSiguiente = document.querySelector('#siguiente');
    const paginaAnterior = document.querySelector('#anterior');

    if(pagina === 1){
        paginaAnterior.classList.add('ocultar'); 
        paginaSiguiente.classList.remove('ocultar');    
    }else if(pagina === 3){
        paginaSiguiente.classList.add('ocultar');
        paginaAnterior.classList.remove('ocultar');
        mostrarResumen(); // estamos en la pagina 3 carga el resumen de la cita 
    }else{
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }
    mostarSeccion(); //llamar la fucnion de mostar seccicion    
}

function mostrarResumen(){

    // Destructuring
     const { nombre, fecha, hora, servicio } = cita;

    // Seleccionar el resumen
    const resumenDiv = document.querySelector('.contenido-resumen');

    // Limpiar el HTML previo
    while( resumenDiv.firstChild ){
        resumenDiv.removeChild( resumenDiv.firstChild);
    }

    // Validacion de obejeto
    if(Object.values(cita).includes('')){
        const noServicios = document.createElement('P');
        noServicios.textContent = 'Faltan datos de servicio, fecha, hora y nombre';

        noServicios.classList.add('invalidar-cita');

        // agregar al resumen div
        resumenDiv.appendChild(noServicios); 
        return;
    }

    const headingCita = document.createElement('H3');
    headingCita.textContent = 'Resumen de Cita';

    //Mostrar el resumen
    const nombreCita = document.createElement('P');
    nombreCita.innerHTML = `<span>Nombre:</span> ${nombre}`; // innerHTML agregar el contenido como html no como mero texto

    const fechaCita = document.createElement('P');
    fechaCita.innerHTML = `<span>Fecha:</span> ${fecha}`;

    const horaCita = document.createElement('P');
    horaCita.innerHTML = `<span>Hora:</span> ${hora}`;

    // variable para agregar los servicios a un div y mistrarlos
    const serviciosCita = document.createElement('DIV');
    serviciosCita.classList.add('resumen-servicio');

    const headingServicios = document.createElement('H3');
    headingServicios.textContent = 'Resumen de Servicios';

    serviciosCita.appendChild(headingServicios);

    let cantidad = 0;

    // Iterar sobre el arreglo de servicio 
    servicio.forEach( servicio => {
        const conetenedorServicio = document.createElement('DIV'); // se crea un div por cada servicio que haya en el arreglo
        conetenedorServicio.classList.add('contenedor-servicio');

        const textoServicio = document.createElement('P');
        textoServicio.textContent = servicio.nombre;

        const precioServicio = document.createElement('P');
        precioServicio.textContent = servicio.precio;
        precioServicio.classList.add('precio');

        const totalServicio = servicio.precio.split('$');
        //console.log(parseInt (totalServicio[1].trim() ));

        cantidad += parseInt (totalServicio[1].trim() ); 

        // colocar texto y precio en el div
        conetenedorServicio.appendChild(textoServicio);
        conetenedorServicio.appendChild(precioServicio);

        serviciosCita.appendChild(conetenedorServicio);

        //console.log(textoServicio);
    });

    resumenDiv.appendChild(headingCita);
    resumenDiv.appendChild(nombreCita);
    resumenDiv.appendChild(fechaCita);
    resumenDiv.appendChild(horaCita);

    resumenDiv.appendChild(serviciosCita);

    const cantidadPagar = document.createElement('P');
    cantidadPagar.classList.add('total');
    cantidadPagar.innerHTML = `<span>Total a Pagar :</span> $ ${cantidad}`;

    resumenDiv.appendChild(cantidadPagar);
}
        
function nombreCita(){
    const nombreInput = document.querySelector('#nombre');

    nombreInput.addEventListener('input', e => {
        const nombreTexto = e.target.value.trim(); // el trim elimina los espacios en blancos colocados en el input

        //console.log(nombreTexto);

        // Validacion del que nombre texto debe tener algo
        if(nombreTexto === '' || nombreTexto.length < 3){
            mostrarAlerta('Nombre no valido', 'error');
        }else{
            const alerta = document.querySelector('.alerta'); // valida en tiempo real que se cumpla la condicion al clase alerta
            if(alerta){
                alerta.remove();
            }
            cita.nombre = nombreTexto;
           // console.log(cita);
        }


    });
}

function mostrarAlerta(mensaje, tipo){

    // Si hay una aletra precia entonces no crear otra 
    const alertaPrevia = document.querySelector('.alerta');
    if(alertaPrevia){
        return; // si ya hay una alerta previa corta el codigo y deja de ejecutarse
    }

    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');

    if(tipo == 'error'){
        alerta.classList.add('error')
    }

    // iyectar al HTML 
    const formulario = document.querySelector('.formulario');
    formulario.appendChild(alerta);

    // eliminar la alerta despues de 3 segundos 
    setTimeout(() => {
        alerta.remove();
    }, 3000);

    //console.log(alerta);
}

function fechaCita(){
    const fechaInput = document.querySelector('#fecha');
    fechaInput.addEventListener('input', e =>{

        const dia = new Date(e.target.value).getUTCDay(); // muestra la fecha mas detalla 
        
        if([0, 6].includes(dia)){
            e.preventDefault();
            fechaInput.value = ''; // resetear el valor
            mostrarAlerta('No prestamos servicio los domingos y sabados', 'error');
        }else{
            cita.fecha = fechaInput.value;
            //console.log(cita);
        }
    })

}

function deshabilitarFechaAnterior(){
    let cero = 0;
    const inputFecha = document.querySelector('#fecha');

    const fechaAhora = new Date();
    const year = fechaAhora.getFullYear();
    const mes = fechaAhora.getMonth() + 1;
    const dia = fechaAhora.getDate();

    const fechaDeshabilitar = `${year}-${cero}${mes}-${dia}`;
    inputFecha.min = fechaDeshabilitar;

    //console.log(fechaDeshabilitar);
}

function horaCita(){
    const inputHora = document.querySelector('#hora');

    inputHora.addEventListener('input', e => {

        const horaCita = e.target.value;
        const hora = horaCita.split(':'); // recorre el arreglo donde esta la hora y lo divide por antes y lo qu esta despues de los 2 puntos :

        if(hora[0] < 9 || hora[0] > 20){
            mostrarAlerta('Servicio desde las 9:00 AM hasta las 8:00 PM', 'error');
            setTimeout(() => {
                inputHora.value = '';  // reseta el input a 00:00 cuando se seleccioa una hora invalidad
            }, 2000);
            //console.log('horas no validad');
        }else{
            cita.hora = horaCita;
            //console.log(cita);
        }

    });
}

